import React from 'react';
import { List, Button, Divider, Checkbox, Card } from 'antd';
import { useHistory } from "react-router-dom";
import { data } from '../../data.js';

const VoteList = () => {
    let history = useHistory();
    const [checkedValues, setcheckedValues] = React.useState([]);

    const onChange = (updatedValues) => {
        console.log(updatedValues);
        if (updatedValues.length >= 10) {
            data.forEach((node) => {
                if (updatedValues.indexOf(node.id) === -1) node.disabled = true;
            })
        } else {
            data.forEach(node => node.disabled = false)
        }
        setcheckedValues(updatedValues);
    }

    const handleClick = (id) => {
        history.push(`/details/${id}`);
    }

    const handleSubmit = () => {
        console.log(checkedValues)
    }

    return (
        <Card
            title="科室名称"
            extra={`${checkedValues.length}/10`}
        >
            <Checkbox.Group style={{ width: '100%' }} onChange={onChange}>
                <List
                    itemLayout="horizontal"
                    dataSource={data}
                    renderItem={item => (
                        <List.Item
                            actions={[<Checkbox value={item.id} disabled={item.disabled || false} >投票</Checkbox>]}
                        >
                            <List.Item.Meta
                                id={item.id}
                                title={item.name}
                                description={item.id}
                                onClick={() => handleClick(item.id)}
                            />
                        </List.Item>
                    )}
                />
            </Checkbox.Group>
            <Divider />
            <Button type="primary" onClick={() => handleSubmit()} block>
                确认投票
            </Button>
        </Card>
    )
}

export default VoteList;
